import {
  Children,
  ReactElement,
  cloneElement,
  isValidElement
} from 'react';

/** Рекурсивно пройтись по дочерним компонентам, выполнив для них переданный метод */
export const recursiveMap = (
  children: ReactElement | ReactElement[],
  fn: (child: ReactElement) => ReactElement
): ReactElement[] => Children.map(children, (child: ReactElement) => {
  if (!isValidElement(child)) {
    return child;
  }

  if ((child as ReactElement).props.children) {
    // eslint-disable-next-line no-param-reassign
    child = cloneElement(child, {}, recursiveMap((child as ReactElement).props.children, fn));
  }

  return fn(child);
});
